import React from 'react'
import './Career.css'

class Product extends React.Component
{
    render(){
        return(
            <div>
                <div className="container">
                
                        <div>
                            <img src="img/career header.png" width="100%" />
                        </div>
                        
                        <div id="career1">
                            <p>Opening Positions</p>
                        </div>
                
                
                        <div className="container">
                            <div className="card" style={{width: 400}}>
                                <img className="card-img-top" src="img/Elfoteam.png" alt="Card image" style={{width: '180px'}, {height: '220px'}} />
                                <div className="card-body">
                                <h2 className="card-title"><b>Noria</b> - Backend Developer</h2>
                                <h4><i>Be a part of Noria's software engineering team in Vietnam</i></h4>
                                    <ul className="card-text">
                                        <li>Work with product development.</li>
                                        <li>Develop frontend and backend software on Noria`s technology stack which includes HTML5, Javascript, Angular, PHP and more.</li>
                                        <li>Focus on  architecture, integrations, usability and performance of highly sophisticated web and mobile applications.</li>
                                        <li>Work in a team with highly skilled colleagues in Norway.</li>
                                        <li>Communicate in English.</li>
                                    </ul>
                                    <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#myModal">Apply</button>
                                </div>
                            </div>
                
                            <div className="card" style={{width: 400}}>
                                <img className="card-img-top" src="img/Imatisteam.png" alt="Card image" style={{width: '180px'}, {height: '220px'}} />
                                <div className="card-body">
                                <h2 className="card-title"><b>Imatis</b> - Frontend Developer</h2>
                                <h4><i>Be a part of Imatis's software engineer team in Vietnam</i></h4>
                                    <ul className="card-text">
                                        <li>Develop new modules in the IMATIS Product suite.</li>
                                        <li>Maintenance existing modules in the IMATIS Product suite.</li>
                                        <li>Develop on Imatis`s technology stack which includes .NET, HTML5, Javascript and more.</li>
                                        <li>Work with product development.</li>
                                        <li>Focus on  architecture, integrations, usability, responsive design and performance of highly sophisticated web and mobile applications.</li>
                                    </ul>
                                    <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#myModal">Apply</button>
                                </div>
                            </div>

                        
                            <div className="card" style={{width: 400}}>
                                <img className="card-img-top" src="img/Noriateam.png" alt="Card image" style={{width: '180px'}, {height: '220px'}} />
                                <div className="card-body">
                                <h2 className="card-title"><b>Elfo</b> - QA/QC</h2>
                                <h4><i>Be a part of Elfo's software engineer team in Vietnam</i></h4>
                                    <ul className="card-text">
                                        <li>Develop ERP applications with Azure, .NET core, C#, ASP.NET MVC, JQuery, MSSQL, Kendo, Vue.js.</li>
                                        <li>Work closely with the CEO and Delivery Manager in Norway to ensure solutions are developed with maintainability and security at the heart.</li>
                                        <li>You will also work with, resolving escalations and diagnosing issues or bugs.</li>
                                        <li>As an experienced Senior Developer at Elfo, you will be responsible for producing a high quality end product.</li>
                                        <li>Communicate in English.</li>
                                    </ul>
                                    <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#myModal">Apply</button>
                                </div>
                            </div>


                            <div className="card" style={{width: 400}}>
                                <img className="card-img-top" src="img/Azetsteam.png" alt="Card image" style={{width: '180px'}, {height: '225px'}} />
                                <div className="card-body">
                                <h2 className="card-title"><b>Azets</b> - Junior Developer</h2>
                                <h4><i>Be a part of Noria's software engineering team in Vietnam</i></h4>
                                    <ul className="card-text">
                                        <li>Work closely with the the team in Norway to ensure solutions are developed with maintainability and security at the heart</li>
                                        <li>Develop frontend and backend software on Azets`s technology stack which includes HTML5, Javascript, Angular, PHP and more.</li>
                                        <li>Assist with web developing.</li>
                                        <li>Work in a team with highly skilled colleagues in Norway.</li>
                                        <li>Communicate in English.</li>
                                    </ul>
                                    <div>
                                        <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#myModal">Apply</button>
                                        <div id="myModal" className="modal fade" role="dialog">
                                            <div className="modal-dialog modal-lg">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                <button type="button" className="close" data-dismiss="modal">X</button>
                                                <h4 className="modal-title" />
                                                </div>
                                                <div className="modal-body">
                                                    
                                                    <div className="form-containn">
                                                     <form id="registerform">
                                                        <h1 id="registerf1">Form for Applicants</h1>
                                                        <div className="form-group">
                                                            <label htmlFor="exampleInputEmail1">Your Email Address* </label>
                                                            <input type="text" className="form-control form-control-lg" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Email" required />
                                                           
                                                        </div>
                                                        <div className="note">
                                                            <b>Why should we choose you for this position</b>
                                                            <textarea id="note"></textarea>
                                                        </div>
                                                        <br />
                            
                                                        <b>Attach your CV here</b>
                                                        <div className="custom-file">
                                                           
                                                            <input type="file" className="custom-file-input" id="validatedCustomFile" required />
                                                            <label className="custom-file-label" htmlFor="validatedCustomFile">Choose file...</label>
                                                            <div className="invalid-feedback">Example invalid custom file feedback</div>
                                                        </div>
                                                        <button type="submit" className="btn btn-primary" id="sm">Submit</button>
                                        
                                                     </form>
                                            </div>
                                               <br />
                                               <br />
                                                </div>
                                                
                                                <p id="salut"> <i>Thank you for your interests in our company, we will review your application and get in touch with you in the earliest time. Regards!</i></p>
                                               
                                            </div>
                                            </div>
                                        </div>
                                        </div>

                              </div>

                            </div>

                            <div id="career1">
                                <p>Our Benefits</p>
                            </div>

                            <div id="benefit">
                                <img src="img/Be1.png" width="50%" height="50%" />
                                <img src="img/Be2.png" width="50%" height="50%" />
                                <img src="img/Be3.png" width="50%" height="50%" />
                                <img src="img/Be4.png" width="50%" height="50%" />
                            </div>


                        </div>
                </div>
                <div className="form-contain">
                            <form id="careerform">
                                    <h1 id="form1"><i>Hey! Do you want to explore more similar positions at our company ?</i></h1>
                                    <div className="form-contain-1">
                                        <div className="form-group">
                                            <label htmlFor="exampleInputEmail1">Your Email</label>
                                            <input type="email" className="form-control form-control-lg" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Email" required />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="exampleInputPhone1">Your Phone number</label>
                                            <input type="number" className="form-control form-control-lg" id="exampleInputPhone1" placeholder="Phone Number" required />
                                        </div>
                                        <div className="form-group">
                                        <label>Which position you are interested in?</label>
                                            <select className="form-control form-control-lg">
                                                <option>Back-end Developer</option>
                                                <option>Front-end Developer</option>
                                                <option>Fullstack Developer</option>
                                                <option>QA/QC</option>
                                            </select>
                                        </div>
                                        <div className="form-group">
                                            <label>Attach your CV here</label>
                                            <div className="custom-file">
                                                <input type="file" className="custom-file-input" id="validatedCustomFile" required />
                                                <label className="custom-file-label" htmlFor="validatedCustomFile">Choose file...</label>
                                                <div className="invalid-feedback">Example invalid custom file feedback</div>
                                            </div>
                                        </div>

                                    </div>
                                    <button type="submit" className="btn btn-secondary"><b>Send Information</b></button>
                            </form>
                        </div>

    </div>
     )
  }
}

export default Product